
import { defineComponent } from 'vue';
export default defineComponent({
    name: 'Message',
    props: {
        username: String,
        message: String,
        createAt: String,
        spin: Boolean,
    },
    methods: {
        tranformDateToTime(date: string) {
            const time = new Date(date);
            return `${time.getHours()}:${time.getMinutes()}`;
        },
    },
});
